.body{
    width:100%;
  
    display: flex;
    flex-direction: column;
     justify-content:center;
    align-content: space-around;
    /* background-color: #eee; */
    padding: 0 1.5rem;
    font-size: 10px;
    /* flex-wrap: wrap; */
    background-color: #D6F4FF;

}

.home_Main{
    min-height: 100vh;
    background-image: linear-gradient(to bottom, #162447, #18294d, #1a2e53, #1b345a, #1d3960, #1f385e, #21375b, #223659, #222f4e, #212843, #1e2139, #1b1b2f);  
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 100px 0px 100px 0px;
   
}

.home_container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    flex-wrap: wrap;
}

.home_item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    width: 100%;
}

.home_item h1 {
    
    font-size: 40px;
    font-weight: bold;
    color: white;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin: 0% 5% 0% 20% ;
    padding: 10px;
}

.home_item p {
    
   
    font-weight: lighter;
    color: slateblue;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    margin: 0% 20% ;
    padding: 10px;
}

.home_item a{
    margin: 0% 20% ;
    padding: 10px;
}

.home_img{
    width: 100%;
    display: flex;
    justify-content: center;
    padding-left: 10px;
}



