.blog-post__cta {
    display: inline-block;
    padding: 5px 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-size: .5rem;
    font-weight: bold;
    color: #fff;
    background-image: linear-gradient(to right,#4facfe 0%
    ,#00f2fe 100%);
    border-radius: .2rem;
    text-decoration: none;
}

